import React from 'react';
import { cssDecorator } from '@front-toolkit/utils';
import { HeaderProps, ModalSize } from 'core/types/modals';
import { IconClose } from 'core/src/icons';
import { Text, TextSize } from 'core/src/typography';

import styles from './styles.scss';

const cd = cssDecorator(styles);

const labelSizeProperty: Record<ModalSize, TextSize> = {
  default: 'default',
  small: 'small'
};

const Header: React.FC<HeaderProps> = ({
  label,
  onClose,
  size = 'default',
  dataTestId = 'header_id'
}) => {
  const labelTextSize = labelSizeProperty[size];
  const sizeClassModifier = { [size]: true };

  return (
    <div className={ styles.wrapper } data-test-id={ dataTestId }>
      <div className={ cd('label', sizeClassModifier) }>
        { typeof label === 'string'
          ? <Text size={ labelTextSize }>{ label }</Text>
          // eslint-disable-next-line react/jsx-no-useless-fragment
          : <>{ label }</> }
      </div>
      {
        onClose && (
          <button
            className={ cd('close_button', sizeClassModifier) }
            type="button"
            onClick={ onClose }
          >
            <IconClose color="primary" />
          </button>
        )
      }
    </div>
  );
};

export default Header;
